import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)


const  list = {
	listing:[],  // 
}

// tab 
const navObj = {
	loading:false,
	nav:'recommend',
	pageId:0,
	uniload: false,
}
// tab 

// vuex 
const state = {
	list, 
	loading: '', // tab 
	navObj,
	nonedata: '',
}

export default new Vuex.Store({
	state,
	actions:{
		listact(listact,data){
			listact.commit('listmut',data);
		}
	},
	mutations:{
		listmut(state,data){
			state.list = {
				listing: data,
			}
		},
		loadMut(state,val){
			state.loading = val;
		},
		navmuat(state,obj){
			state.navObj={
				loading: obj.loading,
				nav: obj.nav,
				pageId:obj.pageId,
				uniload: obj.uniload,
			}
		},
		nonemuat(state,val){
			state.nonedata = val;
		}
	},
})