// promise 
const db = wx.cloud.database();

let home = function(banner){
	return new Promise((resolve,reject)=> {
		const banners = db.collection(banner);
		// get json 
		banners.get().then(res=>{
			resolve(res);
		}).catch(err=>{
			reject(err)
		})
	})
}

let homeList = function(banner,pageId){
	return new Promise((resolve,reject)=> {
		const banners = db.collection(banner)
		.limit(6)
		.skip(pageId*6);
		// get json 
		banners.get().then(res=>{
			resolve(res);
		}).catch(err=>{
			reject(err)
		})
	})
}

export { home,homeList }